export default "@import url(\"https://fonts.googleapis.com/css?family=Josefin+Sans:600&display=swap\");\n.hotels {\n  list-style-type: none;\n  padding: 0;\n  width: 97%;\n  margin: auto;\n}\n.info {\n  text-decoration: none;\n  font-family: \"Josefin Sans\", sans-serif;\n  padding: 0;\n  font-weight: 600;\n  font-size: 2em;\n  color: #FFB300;\n  display: grid;\n  grid-template-areas: \"pic name name\" \"pic stars price\";\n  grid-template-rows: 50% 50%;\n  grid-template-columns: 25% auto auto;\n}\n.stars {\n  grid-area: stars;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n.name {\n  grid-area: name;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n.price {\n  grid-area: price;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n.pic {\n  grid-area: pic;\n  padding: 0;\n  margin: 0;\n}\n.pic img {\n  width: 300px;\n  height: 150px;\n  border-radius: 0 0 0 20px;\n}\n.hotels li {\n  cursor: pointer;\n  position: relative;\n  background-color: #07325a;\n  margin: 0.8em 0 0.8em 0;\n  padding: 0;\n  height: 150px;\n  border-radius: 0 20px 0 20px;\n}\n.hotels li:hover {\n  background-color: rgba(0, 136, 169, 0.8);\n  transition: all 0.3s ease 0s;\n}\n.search {\n  display: grid;\n  grid-template-areas: \" fas form1 dstars dstate form2\";\n  grid-template-columns: 5% 45% 5% 5% 40%;\n  background-color: #07325a;\n  width: 97%;\n  margin: 10px auto 10px auto;\n  padding: 0 10px 0 5px;\n  border-radius: 5px;\n}\n.fa-search {\n  grid-area: fas;\n  padding: 9px;\n  font-size: 20px;\n  width: 40px;\n  text-align: center;\n  text-decoration: none;\n  color: #FFB300;\n}\n.frm1 {\n  grid-area: form1;\n  margin: 0 1% 0 0;\n}\n.frm2 {\n  grid-area: form2;\n  margin: 0 0 0 1%;\n}\n.dropbtn {\n  background-color: #FFB300;\n  color: #07325a;\n  font-size: 20px;\n  width: 40px;\n  height: 40px;\n  border: none;\n  border-radius: 5px;\n  cursor: pointer;\n}\n.dropdown {\n  position: relative;\n  display: inline-block;\n}\n.dstars {\n  grid-area: dstars;\n}\n.dstate {\n  grid-area: dstate;\n}\n.dropdown-content {\n  display: none;\n  position: absolute;\n  background-color: #f9f9f9;\n  min-width: 160px;\n  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);\n  z-index: 1;\n}\n.dropdown-content a {\n  color: #07325a;\n  padding: 12px 16px;\n  text-decoration: none;\n  display: block;\n}\n.dropdown-content a:hover {\n  background-color: #b5e5f3;\n}\n.dropdown:hover .dropdown-content {\n  display: block;\n}\n.dropdown:hover .dropbtn {\n  background-color: #f09c00;\n}\n#sf {\n  display: none;\n}\n@media (max-width: 950px) {\n  .pic img {\n    width: 200px;\n  }\n}\n@media (max-width: 800px) {\n  .search {\n    grid-template-columns: 6% 40% 7% 7% 40%;\n  }\n}\n@media (max-width: 700px) {\n  .info {\n    grid-template-columns: 25% 40% 35%;\n  }\n\n  .stars {\n    justify-content: flex-end;\n  }\n}\n@media (max-width: 600px) {\n  .price span {\n    display: none;\n  }\n\n  .info {\n    grid-template-columns: 35% 40% 25%;\n  }\n\n  .search {\n    grid-template-areas: \" fas dstars form1\" \" fas dstate form2\";\n    grid-template-columns: 10% 10% 80%;\n  }\n\n  .frm1 {\n    margin: 0 0 0 2%;\n  }\n\n  .frm2 {\n    margin: 0 0 0 2%;\n  }\n\n  .pic img {\n    width: 100%;\n  }\n}\n@media (max-width: 460px) {\n  .info {\n    grid-template-areas: \"pic name\" \"pic stars\" \"pic price\";\n    grid-template-rows: 33% 34% 33%;\n    grid-template-columns: 40% 60%;\n    font-size: 1.5em;\n  }\n\n  .stars {\n    justify-content: center;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"